'use client'

import { useEffect, useState, useRef } from 'react'

export default function ServicesSection() {
  const [isVisible, setIsVisible] = useState(false)
  const sectionRef = useRef<HTMLElement>(null)

  useEffect(() => {
    const observer = new IntersectionObserver(
      ([entry]) => {
        if (entry.isIntersecting) {
          setIsVisible(true)
        }
      },
      { threshold: 0.1 }
    )

    if (sectionRef.current) {
      observer.observe(sectionRef.current)
    }

    return () => {
      if (sectionRef.current) {
        observer.unobserve(sectionRef.current)
      }
    }
  }, [])

  const services = [
    {
      title: 'Pengembangan Web',
      description: 'Membangun website modern dan responsif menggunakan teknologi terdepan seperti React, Next.js, dan TypeScript untuk performa optimal.',
      features: ['Responsive Design', 'SEO Optimized', 'Fast Loading', 'Modern Framework']
    },
    {
      title: 'UI/UX Design',
      description: 'Merancang antarmuka pengguna yang intuitif dan pengalaman pengguna yang menyenangkan dengan fokus pada usability dan estetika.',
      features: ['User Research', 'Wireframing', 'Prototyping', 'Design System']
    },
    {
      title: 'Desain Grafis',
      description: 'Menciptakan identitas visual yang kuat untuk brand Anda, mulai dari logo, branding kit, hingga materi marketing yang menarik.',
      features: ['Logo Design', 'Brand Identity', 'Print Design', 'Digital Assets']
    },
    {
      title: 'Konsultasi Digital',
      description: 'Memberikan strategi dan solusi digital yang tepat untuk mengoptimalkan kehadiran online bisnis Anda di era digital.',
      features: ['Digital Strategy', 'Tech Consultation', 'Performance Audit', 'Growth Planning']
    },
    {
      title: 'E-Commerce Solutions',
      description: 'Membangun platform e-commerce yang powerful dan user-friendly untuk meningkatkan penjualan online bisnis Anda.',
      features: ['Online Store', 'Payment Integration', 'Inventory Management', 'Analytics']
    },
    {
      title: 'Maintenance & Support',
      description: 'Layanan pemeliharaan dan dukungan teknis berkelanjutan untuk memastikan website Anda selalu berjalan optimal.',
      features: ['Regular Updates', 'Security Monitoring', 'Performance Optimization', '24/7 Support']
    }
  ]

  return (
    <section id="services" ref={sectionRef} className="py-20 bg-background">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="text-center mb-16">
          <h2 className={`text-4xl md:text-5xl font-bold text-foreground mb-4 transition-all duration-1000 ease-out ${isVisible ? 'opacity-100 translate-y-0' : 'opacity-0 translate-y-10'}`}>
            Layanan Saya
          </h2>
          <div className={`w-24 h-1 bg-primary mx-auto mb-6 transition-all duration-1000 delay-200 ease-out ${isVisible ? 'opacity-100 scale-x-100' : 'opacity-0 scale-x-0'}`}></div>
          <p className={`text-lg text-muted-foreground max-w-3xl mx-auto transition-all duration-1000 delay-300 ease-out ${isVisible ? 'opacity-100 translate-y-0' : 'opacity-0 translate-y-10'}`}>
            Saya menawarkan berbagai layanan digital yang komprehensif untuk membantu bisnis Anda berkembang di era digital
          </p>
        </div>

        <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-8">
          {services.map((service, index) => (
            <div
              key={service.title}
              className={`group bg-card border border-border rounded-xl p-6 shadow-lg hover:shadow-xl transition-all duration-500 ease-out hover:scale-105 hover:border-primary/50 ${isVisible ? 'opacity-100 translate-y-0' : 'opacity-0 translate-y-10'}`}
              style={{ transitionDelay: `${400 + index * 100}ms` }}
            >
              {/* Service Icon/Number */}
              <div className="mb-6">
                <div className="w-16 h-16 bg-gradient-to-br from-primary/20 to-secondary/40 rounded-lg flex items-center justify-center group-hover:from-primary/30 group-hover:to-secondary/50 transition-all duration-300">
                  <span className="text-2xl font-bold text-primary">{String(index + 1).padStart(2, '0')}</span>
                </div>
              </div>

              {/* Service Content */}
              <div className="mb-6">
                <h3 className="text-xl font-bold text-foreground mb-3 group-hover:text-primary transition-colors duration-300">
                  {service.title}
                </h3>
                <p className="text-muted-foreground leading-relaxed">
                  {service.description}
                </p>
              </div>

              {/* Service Features */}
              <div className="space-y-2">
                {service.features.map((feature, featureIndex) => (
                  <div key={feature} className="flex items-center">
                    <div className="w-2 h-2 bg-primary rounded-full mr-3 flex-shrink-0"></div>
                    <span className="text-sm text-muted-foreground">{feature}</span>
                  </div>
                ))}
              </div>

              {/* Hover Effect Overlay */}
              <div className="absolute inset-0 bg-gradient-to-br from-primary/5 to-transparent rounded-xl opacity-0 group-hover:opacity-100 transition-opacity duration-300 pointer-events-none"></div>
            </div>
          ))}
        </div>

        {/* Call to Action */}
        <div className={`text-center mt-16 transition-all duration-1000 delay-1000 ease-out ${isVisible ? 'opacity-100 translate-y-0' : 'opacity-0 translate-y-10'}`}>
          <div className="bg-gradient-to-r from-primary/10 to-secondary/10 rounded-2xl p-8 border border-primary/20">
            <h3 className="text-2xl font-bold text-foreground mb-4">
              Siap untuk Memulai Proyek Anda?
            </h3>
            <p className="text-muted-foreground mb-6 max-w-2xl mx-auto">
              Mari diskusikan kebutuhan proyek Anda dan bagaimana saya dapat membantu mewujudkan visi digital Anda
            </p>
            <button
              onClick={() => {
                const element = document.getElementById('contact')
                if (element) {
                  element.scrollIntoView({ behavior: 'smooth' })
                }
              }}
              className="px-8 py-4 bg-primary text-primary-foreground rounded-lg font-semibold hover:bg-primary/90 transform hover:scale-105 transition-all duration-300 shadow-lg hover:shadow-xl"
            >
              Mulai Konsultasi Gratis
            </button>
          </div>
        </div>
      </div>
    </section>
  )
}
