'use client'

import { useEffect, useState, useRef } from 'react'

export default function AboutSection() {
  const [isVisible, setIsVisible] = useState(false)
  const sectionRef = useRef<HTMLElement>(null)

  useEffect(() => {
    const observer = new IntersectionObserver(
      ([entry]) => {
        if (entry.isIntersecting) {
          setIsVisible(true)
        }
      },
      { threshold: 0.1 }
    )

    if (sectionRef.current) {
      observer.observe(sectionRef.current)
    }

    return () => {
      if (sectionRef.current) {
        observer.unobserve(sectionRef.current)
      }
    }
  }, [])

  return (
    <section id="about" ref={sectionRef} className="py-20 bg-secondary/10">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="text-center mb-16">
          <h2 className={`text-4xl md:text-5xl font-bold text-foreground mb-4 transition-all duration-1000 ease-out ${isVisible ? 'opacity-100 translate-y-0' : 'opacity-0 translate-y-10'}`}>
            Tentang Saya
          </h2>
          <div className={`w-24 h-1 bg-primary mx-auto transition-all duration-1000 delay-200 ease-out ${isVisible ? 'opacity-100 scale-x-100' : 'opacity-0 scale-x-0'}`}></div>
        </div>

        <div className="grid lg:grid-cols-2 gap-12 items-center">
          {/* Content */}
          <div className={`transition-all duration-1000 delay-300 ease-out ${isVisible ? 'opacity-100 translate-x-0' : 'opacity-0 -translate-x-10'}`}>
            <div className="space-y-6">
              <p className="text-lg text-muted-foreground leading-relaxed">
                Saya adalah seorang Web Developer dan UI/UX Designer yang berpengalaman lebih dari 5 tahun 
                dalam industri teknologi. Passion saya terletak pada menciptakan solusi digital yang tidak 
                hanya fungsional, tetapi juga memberikan pengalaman pengguna yang luar biasa.
              </p>
              
              <p className="text-lg text-muted-foreground leading-relaxed">
                Filosofi kerja saya adalah <strong className="text-foreground">"Simplicity is the ultimate sophistication"</strong>. 
                Saya percaya bahwa desain yang baik adalah desain yang sederhana namun powerful, mudah dipahami 
                namun tetap inovatif. Setiap proyek yang saya kerjakan selalu dimulai dengan memahami kebutuhan 
                pengguna dan tujuan bisnis klien.
              </p>

              <p className="text-lg text-muted-foreground leading-relaxed">
                Dengan keahlian dalam teknologi modern seperti React, Next.js, TypeScript, dan berbagai tools 
                desain, saya siap membantu mewujudkan visi digital Anda menjadi kenyataan. Mari berkolaborasi 
                untuk menciptakan sesuatu yang amazing!
              </p>
            </div>

            {/* Skills */}
            <div className="mt-8">
              <h3 className="text-xl font-semibold text-foreground mb-4">Keahlian Utama:</h3>
              <div className="flex flex-wrap gap-3">
                {[
                  'React & Next.js',
                  'TypeScript',
                  'UI/UX Design',
                  'Tailwind CSS',
                  'Node.js',
                  'Figma',
                  'Responsive Design',
                  'API Integration'
                ].map((skill, index) => (
                  <span
                    key={skill}
                    className={`px-4 py-2 bg-primary/10 text-primary rounded-full text-sm font-medium transition-all duration-500 ease-out ${isVisible ? 'opacity-100 translate-y-0' : 'opacity-0 translate-y-5'}`}
                    style={{ transitionDelay: `${600 + index * 100}ms` }}
                  >
                    {skill}
                  </span>
                ))}
              </div>
            </div>
          </div>

          {/* Visual Element */}
          <div className={`transition-all duration-1000 delay-500 ease-out ${isVisible ? 'opacity-100 translate-x-0' : 'opacity-0 translate-x-10'}`}>
            <div className="relative">
              {/* Main Card */}
              <div className="bg-card border border-border rounded-2xl p-8 shadow-lg">
                <div className="text-center">
                  <div className="w-32 h-32 mx-auto mb-6 rounded-full bg-gradient-to-br from-primary/20 to-secondary/40 flex items-center justify-center">
                    <div className="w-24 h-24 rounded-full bg-gradient-to-br from-primary/10 to-secondary/20 flex items-center justify-center">
                      <span className="text-3xl font-bold text-primary">5+</span>
                    </div>
                  </div>
                  <h4 className="text-2xl font-bold text-foreground mb-2">Tahun Pengalaman</h4>
                  <p className="text-muted-foreground">Dalam industri teknologi dan desain</p>
                </div>

                {/* Stats */}
                <div className="grid grid-cols-2 gap-4 mt-8">
                  <div className="text-center p-4 bg-secondary/20 rounded-lg">
                    <div className="text-2xl font-bold text-primary mb-1">50+</div>
                    <div className="text-sm text-muted-foreground">Proyek Selesai</div>
                  </div>
                  <div className="text-center p-4 bg-secondary/20 rounded-lg">
                    <div className="text-2xl font-bold text-primary mb-1">30+</div>
                    <div className="text-sm text-muted-foreground">Klien Puas</div>
                  </div>
                </div>
              </div>

              {/* Decorative Elements */}
              <div className="absolute -top-4 -right-4 w-8 h-8 bg-primary/20 rounded-full"></div>
              <div className="absolute -bottom-4 -left-4 w-6 h-6 bg-secondary/40 rounded-full"></div>
            </div>
          </div>
        </div>
      </div>
    </section>
  )
}
