'use client'

import { useEffect, useState, useRef } from 'react'

export default function PortfolioSection() {
  const [isVisible, setIsVisible] = useState(false)
  const [activeFilter, setActiveFilter] = useState('Semua')
  const sectionRef = useRef<HTMLElement>(null)

  useEffect(() => {
    const observer = new IntersectionObserver(
      ([entry]) => {
        if (entry.isIntersecting) {
          setIsVisible(true)
        }
      },
      { threshold: 0.1 }
    )

    if (sectionRef.current) {
      observer.observe(sectionRef.current)
    }

    return () => {
      if (sectionRef.current) {
        observer.unobserve(sectionRef.current)
      }
    }
  }, [])

  const categories = ['Semua', 'Web Development', 'UI/UX Design', 'Branding', 'E-Commerce']

  const projects = [
    {
      id: 1,
      title: 'E-Commerce Platform Modern',
      category: 'E-Commerce',
      description: 'Platform e-commerce lengkap dengan sistem pembayaran terintegrasi dan dashboard admin yang powerful.',
      technologies: ['Next.js', 'TypeScript', 'Stripe', 'Prisma'],
      image: 'https://images.unsplash.com/photo-1556742049-0cfed4f6a45d?w=600&h=400&fit=crop',
      link: '#',
      status: 'Completed'
    },
    {
      id: 2,
      title: 'Corporate Website Redesign',
      category: 'Web Development',
      description: 'Redesign website corporate dengan fokus pada user experience dan performa yang optimal.',
      technologies: ['React', 'Tailwind CSS', 'Framer Motion'],
      image: 'https://images.unsplash.com/photo-1460925895917-afdab827c52f?w=600&h=400&fit=crop',
      link: '#',
      status: 'Completed'
    },
    {
      id: 3,
      title: 'Mobile Banking App UI',
      category: 'UI/UX Design',
      description: 'Desain antarmuka aplikasi mobile banking yang user-friendly dan aman dengan design system yang konsisten.',
      technologies: ['Figma', 'Design System', 'Prototyping'],
      image: 'https://images.unsplash.com/photo-1512941937669-90a1b58e7e9c?w=600&h=400&fit=crop',
      link: '#',
      status: 'Completed'
    },
    {
      id: 4,
      title: 'Brand Identity Startup Tech',
      category: 'Branding',
      description: 'Pembuatan identitas brand lengkap untuk startup teknologi, termasuk logo, color palette, dan brand guidelines.',
      technologies: ['Adobe Illustrator', 'Brand Strategy', 'Logo Design'],
      image: 'https://images.unsplash.com/photo-1611224923853-80b023f02d71?w=600&h=400&fit=crop',
      link: '#',
      status: 'Completed'
    },
    {
      id: 5,
      title: 'SaaS Dashboard Analytics',
      category: 'Web Development',
      description: 'Dashboard analytics untuk platform SaaS dengan visualisasi data yang interaktif dan real-time updates.',
      technologies: ['React', 'D3.js', 'Node.js', 'MongoDB'],
      image: 'https://images.unsplash.com/photo-1551288049-bebda4e38f71?w=600&h=400&fit=crop',
      link: '#',
      status: 'Completed'
    },
    {
      id: 6,
      title: 'Food Delivery App Design',
      category: 'UI/UX Design',
      description: 'Desain aplikasi food delivery dengan fokus pada kemudahan navigasi dan pengalaman pemesanan yang smooth.',
      technologies: ['Figma', 'User Research', 'Wireframing'],
      image: 'https://images.unsplash.com/photo-1565299624946-b28f40a0ca4b?w=600&h=400&fit=crop',
      link: '#',
      status: 'In Progress'
    }
  ]

  const filteredProjects = activeFilter === 'Semua' 
    ? projects 
    : projects.filter(project => project.category === activeFilter)

  return (
    <section id="portfolio" ref={sectionRef} className="py-20 bg-secondary/10">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="text-center mb-16">
          <h2 className={`text-4xl md:text-5xl font-bold text-foreground mb-4 transition-all duration-1000 ease-out ${isVisible ? 'opacity-100 translate-y-0' : 'opacity-0 translate-y-10'}`}>
            Portfolio Saya
          </h2>
          <div className={`w-24 h-1 bg-primary mx-auto mb-6 transition-all duration-1000 delay-200 ease-out ${isVisible ? 'opacity-100 scale-x-100' : 'opacity-0 scale-x-0'}`}></div>
          <p className={`text-lg text-muted-foreground max-w-3xl mx-auto transition-all duration-1000 delay-300 ease-out ${isVisible ? 'opacity-100 translate-y-0' : 'opacity-0 translate-y-10'}`}>
            Berikut adalah beberapa proyek terbaru yang telah saya kerjakan dengan berbagai klien dan industri
          </p>
        </div>

        {/* Filter Buttons */}
        <div className={`flex flex-wrap justify-center gap-4 mb-12 transition-all duration-1000 delay-400 ease-out ${isVisible ? 'opacity-100 translate-y-0' : 'opacity-0 translate-y-10'}`}>
          {categories.map((category) => (
            <button
              key={category}
              onClick={() => setActiveFilter(category)}
              className={`px-6 py-3 rounded-full font-medium transition-all duration-300 ${
                activeFilter === category
                  ? 'bg-primary text-primary-foreground shadow-lg'
                  : 'bg-card text-foreground hover:bg-primary/10 hover:text-primary border border-border'
              }`}
            >
              {category}
            </button>
          ))}
        </div>

        {/* Projects Grid */}
        <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-8">
          {filteredProjects.map((project, index) => (
            <div
              key={project.id}
              className={`group bg-card border border-border rounded-xl overflow-hidden shadow-lg hover:shadow-2xl transition-all duration-500 ease-out hover:scale-105 ${isVisible ? 'opacity-100 translate-y-0' : 'opacity-0 translate-y-10'}`}
              style={{ transitionDelay: `${600 + index * 100}ms` }}
            >
              {/* Project Image */}
              <div className="relative overflow-hidden">
                <div className="w-full h-48 bg-gradient-to-br from-primary/20 to-secondary/40 flex items-center justify-center">
                  <div className="text-center">
                    <div className="w-16 h-16 bg-primary/20 rounded-lg flex items-center justify-center mx-auto mb-2">
                      <span className="text-2xl font-bold text-primary">P{project.id}</span>
                    </div>
                    <span className="text-sm text-muted-foreground">Project Preview</span>
                  </div>
                </div>
                
                {/* Overlay */}
                <div className="absolute inset-0 bg-primary/80 opacity-0 group-hover:opacity-100 transition-opacity duration-300 flex items-center justify-center">
                  <div className="text-center">
                    <button className="px-6 py-2 bg-white text-primary rounded-lg font-semibold hover:bg-gray-100 transition-colors duration-200 mb-2">
                      Lihat Detail
                    </button>
                    <div className="flex justify-center space-x-2">
                      <span className={`px-2 py-1 text-xs rounded ${project.status === 'Completed' ? 'bg-green-500 text-white' : 'bg-yellow-500 text-white'}`}>
                        {project.status}
                      </span>
                    </div>
                  </div>
                </div>
              </div>

              {/* Project Content */}
              <div className="p-6">
                <div className="flex items-center justify-between mb-2">
                  <span className="text-sm text-primary font-medium">{project.category}</span>
                  <span className={`px-2 py-1 text-xs rounded ${project.status === 'Completed' ? 'bg-green-100 text-green-800 dark:bg-green-900 dark:text-green-200' : 'bg-yellow-100 text-yellow-800 dark:bg-yellow-900 dark:text-yellow-200'}`}>
                    {project.status}
                  </span>
                </div>
                
                <h3 className="text-xl font-bold text-foreground mb-3 group-hover:text-primary transition-colors duration-300">
                  {project.title}
                </h3>
                
                <p className="text-muted-foreground text-sm leading-relaxed mb-4">
                  {project.description}
                </p>

                {/* Technologies */}
                <div className="flex flex-wrap gap-2 mb-4">
                  {project.technologies.map((tech) => (
                    <span
                      key={tech}
                      className="px-2 py-1 bg-secondary/50 text-secondary-foreground text-xs rounded-md"
                    >
                      {tech}
                    </span>
                  ))}
                </div>

                {/* Project Link */}
                <button className="w-full py-2 text-primary font-medium hover:bg-primary/10 rounded-lg transition-colors duration-200">
                  Lihat Proyek â†’
                </button>
              </div>
            </div>
          ))}
        </div>

        {/* Load More Button */}
        <div className={`text-center mt-12 transition-all duration-1000 delay-1000 ease-out ${isVisible ? 'opacity-100 translate-y-0' : 'opacity-0 translate-y-10'}`}>
          <button className="px-8 py-4 border-2 border-primary text-primary rounded-lg font-semibold hover:bg-primary hover:text-primary-foreground transform hover:scale-105 transition-all duration-300">
            Lihat Semua Proyek
          </button>
        </div>
      </div>
    </section>
  )
}
